.DataForm{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 80%;
    margin-left: auto;
    margin-right: auto;
    height: 100%;
    padding: 20px;
    box-sizing: border-box;
}
.databutton{
    margin-top: 20px;
    margin-left: auto;
    margin-right: auto;
    text-transform: inherit !important;
    background-color: linear-gradient(90deg, #f9a00f, #f9a00f) !important;
    color: black;
}
.databutton>button{
    margin-top: 50px;
    margin-left: auto;
    margin-right: auto;
    text-transform: inherit !important;
    background-color: linear-gradient(90deg, #f9a00f, #f9a00f) !important;
    color: black;
}